
export default {
  name: "HeaderComponent",
  props: {
    openModalLogin: Function,
  },
  data() {
    return {
      isOpenMenu: false,
    };
  },
  methods: {
    toggleHeaderMenu() {
      this.isOpenMenu = !this.isOpenMenu;
      if (this.isOpenMenu) {
        document.documentElement.style.overflow = "hidden";
      } else {
        document.documentElement.style.overflow = "";
      }
    },
  },
};
